@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap");

* {
  font-family: "Josefin Sans", sans-serif !important;
}

::-moz-selection {
  background: var(--iq-primary);
  color: var(--iq-white);
  text-shadow: none;
}

::-moz-selection {
  background: var(--iq-primary);
  color: var(--iq-white);
  text-shadow: none;
}

::selection {
  background: var(--iq-primary);
  color: var(--iq-white);
  text-shadow: none;
}

/* Definition Lists */
dl dd {
  margin-bottom: 15px;
}

dl dd:last-child {
  margin-bottom: 0px;
}

.description-text p span,
.description-text h1 span,
.description-text h2 span,
.description-text h3 span,
.description-text h4 span,
.description-text h5 span,
.description-text pre span,
.description-text h6 span {
  color: rgb(255 255 255) !important;
  font-size: 19px !important;
  background-color: rgb(255 255 255 / 0%) !important;
  text-wrap: wrap !important;
}

.sun-editor .se-btn-list.default_value {
  background-color: #143340 !important;
  border: none !important;
}

.sun-editor .se-btn-list:hover {
  background-color: #112935 !important;
}

.sun-editor .se-btn:enabled.on {
  background-color: #112935 !important;
  border-color: #10263cc7 !important;
  color: #ffff !important;
}

.sun-editor .se-btn:enabled:hover {
  background-color: #112935 !important;
  border-color: #10263cc7 !important;
  color: #ffff !important;
}

.sun-editor .se-list-layer {
  background-color: #253942 !important;
}

/* Table */

/* table { border: 1px solid #eaeaea; width: 100%; }
table td, table th { border: 1px solid #eaeaea; } */
a:hover {
  text-decoration: none;
}

.dropdown-item {
  font-size: 14px;
}

.dropdown-menu {
  border-radius: 8px;
}

/* scroller */
.scroller {
  overflow-y: auto;
  scrollbar-color: var(--iq-primary) var(--iq-color-light);
  scrollbar-width: thin;
}

.scroller::-webkit-scrollbar-thumb {
  background-color: var(--iq-primary);
}

.scroller::-webkit-scrollbar-track {
  background-color: var(--iq-color-light);
}

#sidebar-scrollbar {
  overflow-y: scroll;
}

#sidebar-scrollbar::-webkit-scrollbar-thumb {
  background-color: #1c313cc7;
}

#sidebar-scrollbar::-webkit-scrollbar-track {
  background-color: var(--iq-color-light);
}

/* Form */
#sidebar-scrollbar {
  height: 100%;
}

/*---------------------------------------------------------------------
                               Media Support
-----------------------------------------------------------------------*/
.media-support {
  display: inline-block;
  width: 100%;
}

.media-support-user-img img {
  height: 60px;
}

.media-support-header {
  display: flex;
  align-items: flex-start;
}

.media-support-info {
  flex: 1;
}

.projects-media img {
  height: 60px;
}

.iq-media-group .iq-media {
  margin-left: -20px;
  position: relative;
  z-index: 0;
  transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
}

.iq-media-group .iq-media:hover {
  z-index: 9;
}

.iq-media-group .iq-media img,
.iq-media-group .iq-media.border {
  border: 2px solid var(--iq-light-border);
}

.iq-media-group .iq-media:first-child {
  margin-left: 0;
}

.iq-media {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.iq-media:hover {
  text-decoration: none;
}

.media-pl {
  text-align: center;
}

/*---------------------------------------------------------------------
                                  Best Sellers
-----------------------------------------------------------------------*/
.media-sellers {
  display: flex;
  align-items: flex-start;
}

.media-sellers-media-info {
  flex: 1;
}

.media-sellers-img img {
  height: 80px;
}

.iq-card .list-star li {
  margin-right: 0;
}

/*---------------------------------------------------------------------
                                  Profile-Avatar
-----------------------------------------------------------------------*/
.iq-profile-avatar {
  position: relative;
}

.iq-profile-avatar:before {
  content: "";
  position: absolute;
  border-radius: 100%;
  z-index: 1;
  border: 2px solid var(--iq-white);
  background-color: var(--iq-body-text);
  height: 16px;
  width: 16px;
  right: 0;
  bottom: 3px;
}

.iq-profile-avatar.status-online:before {
  background-color: var(--iq-success);
}

.iq-profile-avatar.status-away:before {
  background-color: var(--iq-warning);
}

.avatar-upload {
  position: relative;
  max-width: 205px;
  margin: 15px 0 0;
}

.avatar-upload .avatar-edit {
  position: absolute;
  right: 12px;
  z-index: 1;
  top: 10px;
}

.avatar-upload .avatar-edit input {
  display: none;
}

.avatar-upload .avatar-edit input + label {
  display: inline-block;
  width: 34px;
  height: 34px;
  margin-bottom: 0;
  border-radius: 100%;
  background: var(--iq-white);
  border: 1px solid var(--iq-icon-color);
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
  cursor: pointer;
  font-weight: normal;
  transition: all 0.2s ease-in-out;
}

.avatar-upload .avatar-edit input + label:hover {
  background: var(--iq-border-light);
  border-color: var(--iq-primary);
}

.avatar-upload .avatar-edit input + label:after {
  content: "\f040";
  font-family: "FontAwesome";
  color: var(--iq-body-text);
  position: absolute;
  top: 6px;
  left: 0;
  right: 0;
  text-align: center;
  margin: auto;
}

.avatar-upload .avatar-preview {
  width: 192px;
  height: 192px;
  position: relative;
  border-radius: 100%;
  border: 6px solid var(--iq-light-border);
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
}

.avatar-upload .avatar-preview > div {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

/*---------------------------------------------------------------------
                                  Countdown
-----------------------------------------------------------------------*/
.iq-comingsoon {
  margin-top: 80px;
}

.countdown {
  margin: 60px 0 0;
  padding: 0;
}

.countdown li {
  margin: 0 25px 0 0;
  padding: 45px 0;
  list-style: none;
  width: 22.8%;
  display: inline-block;
  text-align: center;
  line-height: normal;
  background: var(--iq-white);
  float: left;
  color: var(--iq-body-text);
  border-radius: 8px;
}

.countdown li span {
  font-size: 60px;
  display: inline-block;
  width: 100%;
  color: var(--iq-primary);
}

.countdown li:last-child {
  margin-right: 0;
}

.iq-comingsoon-form {
  position: relative;
}

.iq-comingsoon-form button {
  position: absolute;
  right: 0;
  top: 0;
  padding: 9px 30px;
}

/*---------------------------------------------------------------------
                                  Progressbar
-----------------------------------------------------------------------*/
.iq-progress-bar {
  background: var(--iq-progress-light);
  box-shadow: 0 0 0;
  height: 6px;
  margin: 0;
  position: relative;
  width: 100%;
  border-radius: 4px;
}

.iq-progress-bar > span {
  /* background: var(--iq-primary) none repeat scroll 0 0; */
  display: block;
  height: 100%;
  width: 0;
  border-radius: 4px;
}

.iq-progress-bar.iq-progress-bar-icon > span:after {
  content: "";
  width: 12px;
  height: 12px;
  border-radius: var(--border-radius-circle);
  background: var(--white);
  border: 3px solid transparent;
  position: absolute;
  bottom: -3px;
  right: 0;
  z-index: 1;
}

.iq-progress-bar.iq-progress-bar-icon > span.bg-primary:after {
  border-color: var(--iq-primary);
  border-radius: 8px;
}

.iq-progress-bar.iq-progress-bar-icon > span.bg-danger:after {
  border-color: var(--iq-danger);
}

.iq-progress-bar.iq-progress-bar-icon > span.bg-success:after {
  border-color: var(--iq-success);
}

.iq-progress-bar.iq-progress-bar-icon > span.bg-warning:after {
  border-color: var(--iq-warning);
}

.iq-progress-bar.iq-progress-bar-icon > span.bg-info:after {
  border-color: var(--iq-info);
  border-radius: 8px;
}

.iq-progress-bar.iq-progress-bar-icon > span.bg-dark:after {
  border-color: var(--iq-dark);
}

.iq-progress-bar .progress-text {
  position: absolute;
  /* right: -10px; */
  top: -30px;
  font-size: 13px;
  font-weight: 500;
}

/*--------------*/
.iq-card-header select.form-control {
  background: var(--iq-light-border);
  border: none;
  font-size: 14px;
  margin-right: 15px;
}

.iq-card-header select.form-control.width-100 {
  width: 180px;
}

/*---------------------------------------------------------------------
                             Badge Img Tag
-----------------------------------------------------------------------*/
.badge-img-tag {
  position: absolute;
  top: 15px;
  left: 15px;
}

/*---------------------------------------------------------------------
                             list Style 1
-----------------------------------------------------------------------*/
.iq-list-style-1 {
  margin: 0;
  padding: 0;
}

.iq-list-style-1 li {
  display: inline-block;
  float: left;
  list-style-type: none;
  margin-right: 15px;
}

.iq-list-style-1 li:last-child {
  margin-right: 0;
}

.iq-list-style-1 li a {
  color: var(--iq-icon-color);
}

.iq-list-style-1 li a i {
  color: var(--iq-icon-color);
  margin-right: 5px;
}

.iq-list-style-1 li a:hover,
.iq-list-style-1 li a:focus {
  text-decoration: none;
  color: var(--iq-primary);
}

.iq-list-style-1.text-white li a,
.iq-list-style-1.text-white li a i {
  color: var(--iq-white);
}

.list-inline.text-white li a {
  color: var(--iq-white);
}

.list-inline.text-white li a:hover {
  opacity: 0.6;
  text-decoration: none;
}

/*---------------------------------------------------------------------
                             Icon Box
-----------------------------------------------------------------------*/
.iq-icon-box {
  height: 60px;
  width: 60px;
  line-height: 60px;
  text-align: center;
  margin-right: 10px;
  font-size: 22px;
  text-decoration: none;
}

.iq-icon-box:hover {
  text-decoration: none;
}

/*---------------------------------------------------------------------
                             Tasks Card
-----------------------------------------------------------------------*/
.tasks-card {
  padding: 0 0 15px 0;
  border-bottom: 1px solid var(--iq-light-border);
  margin: 0 0 15px 0;
}

.tasks-card .close {
  font-size: 16px;
}

.tasks-card:last-child {
  border: none;
  margin: 0;
  padding: 0;
}

/*---------------------------------------------------------------------
                               Sign In
-----------------------------------------------------------------------*/
.sign-in-detail {
  color: var(--iq-white);
  background: rgba(130, 122, 243, 1) !important;
}

.sign-in-page {
  height: 100vh;
}

.sign-in-detail {
  padding: 100px;
  height: 100vh;
}

.sign-in-logo {
  display: inline-block;
  width: 100%;
}

.sign-in-logo img {
  height: 50px;
}

.sign-in-from {
  padding: 0 60px;
}

.sign-info {
  border-top: 1px solid var(--iq-dark-border);
  margin-top: 30px;
  padding-top: 20px;
}

.iq-social-media {
  margin: 0;
  padding: 0;
  float: right;
}

.iq-social-media li {
  list-style: none;
  float: left;
  margin-right: 10px;
}

.iq-social-media li:last-child {
  margin-right: 0;
}

.iq-social-media li a {
  height: 30px;
  width: 30px;
  text-align: center;
  font-size: 18px;
  line-height: 30px;
  display: inline-block;
  -webkit-border-radius: 7px;
  -moz-border-radius: 7px;
  border-radius: 7px;
  background: rgba(130, 122, 243, 0.2);
  color: var(--iq-primary) !important;
}

.iq-social-media li a:hover {
  text-decoration: none;
}

.dash-hover-blank:hover {
  cursor: pointer;
  background-color: var(--iq-light-border);
}

/*---------------------------------------------------------------------
                               Waves Effect
-----------------------------------------------------------------------*/
.iq-waves-effect {
  position: relative;
  overflow: hidden;
}

/*  Ripple */
.ripple {
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(71, 92, 199, 0.1);
  transform: scale(0);
  position: absolute;
  opacity: 1;
}

.rippleEffect {
  animation: rippleDrop 0.6s linear;
}

@keyframes rippleDrop {
  100% {
    transform: scale(2);
    opacity: 0;
  }
}

/*---------------------------------------------------------------------
                                Sidebar
-----------------------------------------------------------------------*/
.iq-sidebar {
  position: fixed;
  z-index: 999;
  top: 0;
  width: 268px;
  height: 100%;
  background: var(--iq-card-bg);
  transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -webkit-box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
  -moz-box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
  box-shadow: 0px 0px 25px 0px rgba(45, 69, 95, 0.1);
}

.iq-sidebar .iq-sidebar-logo {
  float: none;
  width: 100%;
}

.iq-sidebar #sidebar-scrollbar {
  margin-top: 15px;
}

/*---------------------------------------------------------------------
                             Sidebar Menu
-----------------------------------------------------------------------*/

/* Form */
#sidebar-scrollbar {
  height: 90vh;
}

.scroll-content div.p-3 {
  height: 100px;
}

.iq-sidebar-menu .iq-menu .iq-menu-title {
  display: block;
  color: var(--iq-body-text);
  font-size: 15px;
  position: relative;
  padding: 15px 20px;
  font-weight: 500;
  line-height: 18px;
}

.iq-sidebar-menu {
  position: relative;
}

.iq-sidebar-menu .iq-menu {
  margin: 0 15px;
  padding: 0;
  list-style-type: none;
  text-transform: capitalize;
}

.iq-sidebar-menu .iq-menu li {
  list-style-type: none;
  position: relative;
  white-space: nowrap;
}

.iq-sidebar-menu .iq-menu li a {
  font-size: 18px;
  white-space: nowrap;
  display: block;
  color: var(--iq-icon-color);
  position: relative;
  padding: 15px 20px;
  line-height: 18px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  overflow: hidden;
  border-radius: 6px;
}

.iq-sidebar-menu .iq-menu li li a {
  font-size: 14px;
}

.iq-sidebar-menu .iq-menu li a i {
  font-size: 20px;
  margin-right: 10px;
  vertical-align: middle;
  width: 20px;
  display: inline-block;
  float: left;
}

.iq-sidebar-menu .iq-menu li li a i {
  font-size: 16px;
}

.iq-sidebar-menu .iq-menu li a span {
  white-space: nowrap;
  padding: 0;
  display: inline-block;
  float: left;
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
  transition: none;
  /* font-size: 15px; */
}

.iq-sidebar-menu .iq-menu li a .iq-arrow-right {
  margin-right: 0;
  margin-left: auto;
}

.iq-sidebar-menu .iq-menu li a .badge {
  position: absolute;
  right: 15px;
  -webkit-transition: all 0.9s ease-out 0s;
  -moz-transition: all 0.9s ease-out 0s;
  -ms-transition: all 0.9s ease-out 0s;
  -o-transition: all 0.9s ease-out 0s;
  transition: all 0.9s ease-out 0s;
}

.iq-sidebar-menu .iq-menu > li.active {
  border-bottom: none;
  margin-bottom: 10px;
}

.iq-sidebar-menu .iq-menu li a:hover {
  color: var(--iq-primary);
}

.iq-sidebar-menu .iq-menu li.active > a {
  color: var(--iq-white);
  text-decoration: none;
  background: var(--iq-primary);
  text-decoration: none;
}

.iq-search-bar .searchbox .search-link {
  position: absolute;
  right: 15px;
  top: 2px;
  font-size: 16px;
}

.iq-sidebar-menu .iq-menu li a[aria-expanded="true"] {
  color: var(--iq-white);
  /* color: #7a6699; */
  text-decoration: none;
  background: var(--iq-primary);

  transition: none;
  border-radius: 0 8px 0 0;
}

.iq-sidebar-menu .iq-menu li a[aria-expanded="true"] .iq-arrow-right {
  transform: rotate(90deg);
}

.iq-sidebar-menu .iq-menu li .iq-submenu li a:hover,
.iq-sidebar-menu .iq-menu li.active .iq-submenu li > a {
  background: transparent;
}

.iq-sidebar-menu .iq-menu li.active .iq-submenu li.active > a {
  background: var(--iq-light-primary);
  color: var(--iq-primary);
  border-radius: 0;
}

.iq-sidebar-menu .iq-menu li .iq-submenu li > a::before {
  display: none;
}

ul#topbar-data-icon {
  margin: -60px 10px 0;
}

.topbar-menu-icon li a {
  background: var(--iq-card-bg);
  padding: 5px 10px;
  margin: 0 0 0 15px;
  border-radius: 8px;
}

.searchbox-datalink {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 100%;
  left: 0;
  background: var(--iq-card-bg);
  width: 100%;
  max-height: 300px;
  overflow-y: scroll;
  border-radius: 8px;
  box-shadow: 0px 0px 12px 0px rgba(120, 146, 141, 0.06);
}

.searchbox-datalink.show-data {
  opacity: 1;
  visibility: visible;
  animation: fade-in-bottom 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  transform: translateY(100%);
}

/*------------------------*/
.iq-sidebar-menu .iq-menu li ul {
  padding-left: 0px;
}

.iq-sidebar-menu .iq-menu li ul li a {
  color: var(--iq-icon-color);
}

.iq-sidebar-menu .iq-menu li ul a:hover {
  color: var(--iq-primary);
  text-decoration: none;
  background: transparent;
}

.iq-sidebar-menu .iq-menu .iq-submenu {
  background: var(--iq-body-bg);
  border-radius: 0 0 8px 0;
}

.iq-sidebar-menu .iq-menu li ul.iq-submenu-data li > a {
  padding-left: 50px;
}

.iq-sidebar-menu .iq-menu li.active .iq-submenu li a[aria-expanded="true"],
.iq-sidebar-menu .iq-menu li .iq-submenu li a[aria-expanded="true"] {
  background: var(--iq-icon-color);
  border-radius: 0;
  color: var(--iq-white);
}

.iq-sidebar-menu .iq-menu li.active ul.iq-submenu-data li.active > a {
  background: var(--iq-light-primary);
  color: var(--iq-primary);
  border-radius: 0;
}

.iq-sidebar-menu .iq-menu li ul.iq-submenu-data li {
  margin: 0;
}

body.sidebar-main
  .iq-sidebar
  .iq-sidebar-menu
  .iq-menu
  li
  ul.iq-submenu-data
  li
  > a {
  padding-left: 15px;
}

body.sidebar-main
  .iq-sidebar:hover
  .iq-sidebar-menu
  .iq-menu
  li
  ul.iq-submenu-data
  li
  > a {
  padding-left: 50px;
}

